/*
  -------------------------------------------------------
   Imports:
  -------------------------------------------------------
*/
import ButtonBox from "components/ButtonBox";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import InputBox from "components/InputBox";
import InputTextArea from "components/InputTextArea";
import PageTitle from "components/PageTitle/PageTitle";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import ToastCustom from "components/Toast/Toast";
import { RouteProps, withRouter } from "hoc/withRouter";
import { ToastPositionType } from "primereact/toast";
import queryString from "query-string";
import { useEffect, useRef, useState } from "react";
import { linkOrganizationDepartment } from "routes";
import SimpleReactValidator from "simple-react-validator";
import { getStandardValidator, messageReplace, validateGetAPIResponse } from "utils/CommonUtil";
import {
  ACTIVE_TYPE,
  BUTTON_TYPE,
  DROPDOWN_STYLE_TYPE,
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  STATUS_LIST,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import OrgStructureDepartmentService from "services/masterdata/OrgStruct/Department/Department";
import AclGuard from "components/AclGuard/AclGuard";

/*
  -------------------------------------------------------
  PROPS:
  -------------------------------------------------------
*/

const OrganizationLevelDepartmentCreateViewEdit = (props: RouteProps) => {
  /*
    -------------------------------------------------------
      States
    -------------------------------------------------------
  */
  const [pageObj, setPageObj] = useState({
    displayName: "",
    systemCode: "",
    description: "",
    reasons: "",
    isActive: "",
  });
  const [uid, setUid] = useState<string>();
  const [pageMode, setPageMode] = useState(PAGE_MODE.CREATE);
  const [isNameDuplicated, setIsNameDuplicated] = useState(false);
  const [isCodeDuplicated, setIsCodeDuplicated] = useState(false);
  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right");
  const [toastMessage, setToastMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [triggerToast, setTriggerToast] = useState(false);
  const [navigateBack, setNavigateBack] = useState(false);
  const toastMessageRef = useRef(null);
  const [, forceUpdate] = useState<undefined | boolean>(undefined);
  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );
  const [intialObj, setInitialObj] = useState({
    displayName: "",
    systemCode: "",
    description: "",
    reasons: "",
    isActive: "",
  });

  /*
      -------------------------------------------------------
        Hooks: useEffect
      -------------------------------------------------------
    */
  useEffect(() => {
    const parsed: {
      mode?: string;
    } = queryString.parse(props.location.search);
    if (parsed?.mode && parsed?.mode !== undefined) {
      setPageMode(parsed.mode);
    }
  }, []);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore :i ll do it later
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    const parsed: {
      mode?: string;
      uid?: string;
    } = queryString.parse(props.location.search);
    if (parsed?.mode && parsed?.mode !== undefined) {
      setPageMode(parsed.mode);
    }
    if (parsed?.uid && parsed?.uid !== undefined) {
      setUid(parsed.uid);

      OrgStructureDepartmentService.findById(parsed.uid)
        .then((res: any) => {
          if (validateGetAPIResponse(res)) {
            console.log(res);
            if (Object.keys(res.data).length > 0 || res.data.id) {
              setPageObj({
                displayName: res?.data?.name ?? "",
                systemCode: res?.data?.code ?? "",
                description: res?.data?.description ?? "",
                reasons: res?.data?.reason ?? "",
                isActive: res?.data?.isActive ? ACTIVE_TYPE.ACTIVE : ACTIVE_TYPE.INACTIVE,
              });
              setInitialObj({ ...res?.data });
            }
          }
        })
        .catch((err) => {
          // IF API fails to load, show the error message
        });
    }
  }, []);

  /* 
    -----------------------------------------------------------------------------------------------------------------------
    Validators:
    -----------------------------------------------------------------------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = async () => {
    let isValid = false;

    isValid = simpleValidator?.current?.allValid() && !isNameDuplicated && !isCodeDuplicated;
    if (isValid) {
      return isValid;
    } else {
      setPosition("top-right");
      setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.ERROR);
      setTriggerToast(!triggerToast);
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
      -------------------------------------------------------
        Handlers: Event handler for onChange, onBlur etc
      -------------------------------------------------------
    */

  const handleInputChange = (event: any) => {
    const { name, value } = event.target;
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      [name]: value,
    }));
  };

  /* 
      -------------------------------------------------------
        ButtonClick Functions:
      -------------------------------------------------------
    */
  const onSaveClick = async () => {
    let isValid = false;
    isValid = await validateForm();
    if (!isValid) {
      return;
    }
  };

  const onCancelClick = () => {
    props.navigate(linkOrganizationDepartment);
  };

  const onApproveClick = () => {
    console.log("this is onApproveClick");
  };

  const onBackClick = () => {
    props.navigate(linkOrganizationDepartment);
  };

  const onEditClick = () => {
    setPageMode(PAGE_MODE.EDIT);
  };

  /* 
      -------------------------------------------------------
        Helper Functions:
      -------------------------------------------------------
    */
  const getPageTitle = () => {
    switch (pageMode) {
      case PAGE_MODE.CREATE:
        return "heading.organizationLevelDepartment.create";
      case PAGE_MODE.VIEW:
        return "heading.organizationLevelDepartment.view";
      case PAGE_MODE.EDIT:
        return "heading.organizationLevelDepartment.edit";
      default:
        return "heading.organizationLevelDepartment.create";
    }
  };

  /* 
    -------------------------------------------------------
      Sectional Render Functions: For Sub components / section wise renders
    -------------------------------------------------------
  */
  const { displayName, systemCode, description, reasons, isActive } = pageObj;
  return (
    <AclGuard override={{ read: PAGE_MODE.VIEW === pageMode }}>
      <div className="flex flex-col gap-3">
        <div className="flex w-full justify-between items-center">
          <PageTitle title={getPageTitle()} />
          <div className="flex">
            {pageMode === PAGE_MODE.CREATE || pageMode === PAGE_MODE.EDIT ? (
              <div className="flex gap-10">
                <div className="flex gap-5">
                  <ButtonBox buttonType={BUTTON_TYPE.CANCEL_OUTLINE} onClick={onCancelClick} />

                  <ButtonBox buttonType={BUTTON_TYPE.SAVE_OUTLINE} onClick={onSaveClick} />

                  <div></div>
                  <ButtonBox buttonType={BUTTON_TYPE.SEND_FOR_APPROVAL} onClick={onApproveClick} />
                </div>
              </div>
            ) : (
              <>
                <div className="mr-5">
                  <ButtonBox buttonType={BUTTON_TYPE.BACK_OUTLINE} onClick={onBackClick} />
                </div>
                <div className="">
                  <ButtonBox buttonType={BUTTON_TYPE.EDIT_OUTLINE} onClick={onEditClick} />
                </div>
              </>
            )}
          </div>
        </div>

        <div className="grid grid-cols-4 gap-3">
          <div className="col-span-1">
            <InputBox
              label="input.organizationLevelDepartment.displayName"
              name="displayName"
              variant={INPUT_STYLE_TYPE.SECONDARY}
              value={displayName}
              disabled={pageMode === PAGE_MODE.VIEW}
              onChange={handleInputChange}
              onBlur={validate}
              validation={
                isNameDuplicated
                  ? "Duplicate Entry Found"
                  : simpleValidator?.current?.message("displayName", displayName, "mandatory")
              }
            />
          </div>
          <div className="col-span-1">
            <InputBox
              label="input.organizationLevelDepartment.systemCode"
              name="systemCode"
              variant={INPUT_STYLE_TYPE.SECONDARY}
              value={systemCode}
              disabled={pageMode === PAGE_MODE.VIEW}
              onBlur={validate}
              onChange={handleInputChange}
              validation={
                isCodeDuplicated
                  ? "Duplicate Entry Found"
                  : simpleValidator?.current?.message(
                      "systemCode",
                      systemCode,
                      "onlyAlphaNumUnderscoreUpperScore|mandatory"
                    )
              }
            />
          </div>
          {pageMode !== PAGE_MODE.CREATE ? (
            <div className="col-span-1">
              <SimpleDropDown
                disabled={pageMode === PAGE_MODE.VIEW}
                label="input.organizationLevelDepartment.isActive"
                name="isActive"
                variant={DROPDOWN_STYLE_TYPE.SECONDARY}
                value={isActive}
                options={STATUS_LIST}
                onChange={handleInputChange}
              />
            </div>
          ) : (
            <div className="col-span-2" />
          )}
          <div className="col-span-2">
            <InputTextArea
              label="input.organizationLevelDepartment.description"
              name="description"
              value={description}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              disabled={pageMode === PAGE_MODE.VIEW}
              onBlur={validate}
              onChange={handleInputChange}
            />
          </div>
          {pageMode !== PAGE_MODE.VIEW && (
            <div className="col-span-2">
              <InputTextArea
                label="input.organizationLevelDepartment.reasons"
                name="reasons"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                value={reasons}
                onChange={handleInputChange}
                onBlur={validate}
                validation={simpleValidator?.current?.message("Reasons", reasons, "mandatory")}
              />
            </div>
          )}
        </div>
      </div>
      <div className="">
        <ToastCustom
          position={position}
          ref={toastMessageRef}
          toastText={toastMessage}
          variant={toastMessageVariant}
          onRemove={() => {
            if (navigateBack) {
              props.navigate(linkOrganizationDepartment);
            }
          }}
        />
      </div>
      <ErrorDialog
        onHide={() => setShowDialogAPIError(false)}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </AclGuard>
  );
};

export default withRouter(OrganizationLevelDepartmentCreateViewEdit);
